.board-container {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.board {
  grid-gap: 24px;
  perspective: 800px;
  background: linear-gradient(#3d7dca, #003a70);
  border-radius: 24px;
  grid-template-rows: repeat(4, 8rem);
  grid-template-columns: repeat(4, 8rem);
  padding: 24px;
  display: grid;
}

.pkm-card {
  transform-style: preserve-3d;
  transition: all 2s cubic-bezier(.175, .885, .32, 1.275);
  position: relative;
}

.face, .back {
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-radius: 8px;
  position: absolute;
  box-shadow: 0 5px 15px #0003;
}

.face {
  background-color: #fff;
  padding: 16px;
}

.back {
  backface-visibility: hidden;
  background-color: #ffcb05;
}

.flip {
  transform: rotateY(180deg);
}

.stats-container {
  text-align: center;
  justify-content: center;
  gap: 24px;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.stat-badge {
  color: #fff;
  width: 100px;
  background-color: #003a70;
  border-radius: .25rem;
  padding: .375rem .75rem;
  font-weight: 700;
}

.reset {
  cursor: pointer;
}

.stats-modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #0006;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  width: 50%;
  background-color: #fff;
  border: 16px solid #ffcb05;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 88px auto;
  display: flex;
}

.result-modal-img {
  width: 240px;
  margin: 16px 0;
  display: inline-block;
}

.reload-btn {
  margin-bottom: 16px;
}

.stats {
  margin: 0;
  font-weight: 700;
}

.stats span {
  color: #003a70;
}

.stats:last-child {
  margin-bottom: 16px;
}

.stats h2 {
  margin-top: 16px;
}

img {
  user-drag: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  user-select: none;
}

@media (max-width: 768px) {
  .board {
    grid-template-rows: repeat(4, 7rem);
    grid-template-columns: repeat(4, 7rem);
  }
}

@media (max-width: 612.98px) {
  .board {
    grid-template-rows: repeat(4, 5rem);
    grid-template-columns: repeat(4, 5rem);
  }
}

@media (max-width: 481.98px) {
  .board {
    grid-template-rows: repeat(4, 3.5rem);
    grid-template-columns: repeat(4, 3.5rem);
  }

  .face {
    padding: 8px;
  }
}

